import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import InfoTableItem from "../../plugin/components/InfoTableItem";
export default {
  name: "SetAward",
  extends: BaseIframe,
  components: { PermissionButton, InfoTable, InfoTableItem },
  data() {
    return {
      format: {
        active: false,
        begin_time: ""
      },
      form: {
          name: ""
      },
      //表格数据
      tableStyle: [
        { label: "ID", prop: "user_id", width: "" },
        { label: "金额", prop: "money", width: "" },
        { label: "概率", prop: "chance", width: "" },
        { label: "操作", prop: "action", width: "" }
      ],
      records: [
        {
          user_id: "0131561",
          money: "10000.00",
          chance: "1",
          action: [{ label: "修改", type: "edit" },{label: "删除",type: "delete"}]
        }
      ],
      pageInfo: new PageInfo(0, [5, 10, 15], 0), // page pageSizes total
      dialogTitleType:'每日签到设置',
      dialogVisible:false,
      userLevels:[],
      labelWidth:'100px',
      formDates:{
        level:'',
        day1:'',
        day2:'',
        day3:'',
        day4:'',
        day5:'',
        day6:'',
        day7:''
      }
    };
  },
  methods: {
    search() {},
    confirm(){

    }
  }
};



